.page {
    position: absolute;
    background-color: #f7f3ec;
    width: 100%;
    min-height: 100vh;
    z-index: 2;
}

::selection {
    background-color: #f596aa;
    color: #f7f3ec;
}

* {
    transition: text-shadow 0.5s ease;
  }
  
  .page::before {
    content: "";
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -9999; /* Sit on top */
  
    background: linear-gradient(to top right, #f596aa90, #0A695590);
    /*background-image: url('./imgs/background/mika_hengtu.png');图片太大了，先注释掉，以后服务器好了再用*/
    background-size: cover;
    background-position: center;
  }

  /*竖着排列字符*/
  .vertical-text span {
    display: block;
}

/*页面加载动画*/
  * {
    animation-name: fadeIn;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    cursor: none !important;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
/* 设置全局的鼠标闪烁指针颜色 */
* {
  caret-color: #f7f3ec;
}

/* 自定义鼠标样式 */
.custom-cursor {
  position: fixed;
  width: 10px;
  height: 10px;
  background-color: #f596aa;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 9999;  /* 确保在所有其他元素之上 */
  animation-name: ripple;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
  pointer-events: none;
}
/* 设置滚动条轨道的样式 */
*::-webkit-scrollbar-track {
  background-color: #f7f3ec;
}

*::-webkit-scrollbar {
  width: 8px;
}

/* 设置滚动条滑块的样式 */
*::-webkit-scrollbar-thumb {
  background-color: #fedfe1;
  border-radius: 3px;
}

/* 自定义鼠标背景 */
.custom-cursor-bg {
  position: fixed;
  width: 20px;
  height: 20px;
  background-color: #f7f3ec50;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 9998;
  transition: top 0.05s, left 0.05s;  /* 延迟效果 */
  animation-name: ripple-bg;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
}

/*从小变大动画*/
.scale-up {
  animation-name: scaleUp;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

@keyframes scaleUp {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

.visible {
  display: block;
  animation-name: none; /* 取消动画 */
}

.hidden {
  display: none;
  animation-name: ripple, ripple-bg; /* 应用动画 */
}


@keyframes ripple {
  0% {
    width: 10px;
    height: 10px;
    opacity: 1;
  }
  100% {
    width: 50px;
    height: 50px;
    opacity: 0;
  }
}

@keyframes ripple-bg {
  0% {
    width: 20px;
    height: 20px;
    opacity: 0.5;
  }
  100% {
    width: 100px;
    height: 100px;
    opacity: 0;
  }
}
