#snowflakes-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 9999;
    pointer-events: none;
  }
  
  .snowflake {
    position: absolute;
    background-color: #f7f3ec50;
    width: 0.8vh;
    height: 0.8vh;
    border-radius: 50%;
    z-index: 9999;
    pointer-events: none;
  }
  