.nav {
  position: fixed;
  width: 100%;
  -webkit-text-stroke: 0.1px #000000; 
  display: flex;
  justify-content: center;
  z-index: 1;
}

@keyframes radial-pulse {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.down {
  background: radial-gradient(circle at center, #f596aa90, #f7f3ec10);
  animation: radial-pulse 0.5s ease-in-out forwards;
}


/* 设置导航栏的背景颜色和字体 */
.navup {
  position: fixed;
  top: 0.5vh;
    }

li {
  list-style-type: none;
  text-shadow: 1px 1px 10px #00000050;
}

.nav .navdown{
  display: none;
}

.Search {
  position: absolute;
  right: 1%;
  top: 0.5vh;
}

.Homebt {
  position: absolute;
  font-size: 2em;
  text-shadow: 1px 1px 10px #00000050;
  animation: slide-in 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55) forwards;
  padding: 1vh;
  text-decoration: none;
  color: #f4aa9a;
}

.Homebt:hover {
  color: #f4aa9a;
  text-shadow: 3px 3px 5px #f596aa;
  cursor: pointer;
}

.Homebtdown{
  position: relative;
  animation: slide-out 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55) forwards;
}

@keyframes slide-in {
  0% { left: -5%; }
  25% { left: 5%; }
  50% { left: -2.5%; }
  75% { left: 2.5%; }
  100% { left: 1%; }
}

@keyframes slide-out {
  0% { left: 1%; }
  25% { left: -2.5%; }
  50% { left: 2.5%; }
  75% { left: -1.25%; }
  100% { left: 0; }
}


  /* 设置每个导航链接的样式 */
  li a {
    font-size: larger;
    padding: 1vh;
    text-decoration: none;
    color: #f4aa9a;
  }
  
  /* 添加鼠标悬停时的样式 */
  li a:hover {
    color: #f4aa9a;
    text-shadow: 3px 3px 5px #f596aa;
    cursor: pointer;
  }