.welcome-area {
  position: relative;
  top: 30vh;
  width: 100%;
  text-align: center;  /* 文本居中 */
  z-index: -2;
}

.welcome-area h1 {
  font-size: 5vw;
  color: #f7f3ec;
  text-shadow: 1px 1px 5px #000000;
  margin-left: auto;
  margin-right: auto;
}

.typing-effect {
  font-size: 3.5vw; 
}

.cursor {
  display: inline-block;
  width: 0.1em;
  height: 3.5vw;  /* 与文本高度相同 */
  background-color: black;
  animation: blink 1s steps(1) infinite;
  vertical-align: bottom;
  margin-bottom: 1vh;  /* 或者使用 margin-top 进行微调 */
}


@keyframes blink {
  50% {
    background-color: transparent;
  }
}
