.Home {
    position: absolute;
    min-height: 100%;
    width: 100%;
    z-index: -1;
}

.Home::before {
    content: "";  /* 添加内容属性 */
    position: absolute;
    left: 0;  /* 添加左定位 */
    top: 0;  /* 添加上定位 */
    background-color: #f596aa50;
    width: 100%;
    height: 100%;
    z-index: -2;  /* 更改z-index */
  }