.bar-container {
    width: 20vw;
    background-color: #f7f3ec;
    height: 20px;
    border-radius: 5px;
    min-width: 100%;
}

.performance-bar {
    height: 100%;
    background-color: #fedfe1;
    border-radius: 5px;
}
.comparison-bar {
    margin-bottom: 20px;
}

table {
    min-width: 100%;
    border-collapse: collapse;
}

th, td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.cpuname {
    width: 20vw;
}

tr:hover {
    background-color: #f5f5f5;
}

input[type="text"] {
    padding: 10px;
    margin-bottom: 20px 0;
    min-width: 15vw;
    border-radius: 10px;
    box-sizing: border-box;
    background-color: #f7f3ec;
    caret-color: black;
}
/* 隐藏默认的勾选框样式 */
input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    position: relative;
    width: 20px;
    height: 20px;
    background-color: #fedfe1;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    box-shadow: 0 0 0px transparent;
    transition: box-shadow 0.5s;
}

/* 为选中的勾选框添加一个钩子 */
input[type="checkbox"]:checked::before {
    content: '✔';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #f3a7ac;
    font-size: 18px;
}
/* 当鼠标悬停在勾选框上时，添加一个黑色的影子 */
input[type="checkbox"]:hover {
    box-shadow: 0 0 10px #612b2b;
}


.comparison-bar span {
    background-color: #f2f2f9;
    padding: 5px 10px;
    border-radius: 5px;
    min-width: 100%;
}
.data-container {
    height: 50vh;
    overflow-y: scroll;
    min-width: 100%;
}
.no-animation, .no-animation * {
    animation-name: none !important;
    animation-duration: 0s !important;
}
/* 设置滚动条轨道的样式 */
.data-container::-webkit-scrollbar-track {
    background-color: #f7f3ec;
}

.data-container::-webkit-scrollbar {
    width: 8px;
}

/* 设置滚动条滑块的样式 */
.data-container::-webkit-scrollbar-thumb {
    background-color: #fedfe1;
    border-radius: 3px;
}
