/*精选整体布局*/
.separator {
    position: absolute;
    top: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 5vh;
    padding-bottom:5vh;
}

.featured {
  position: relative;
  color: #f7f3ec;
  border-radius: 10px;
  background-color: #f4aa9a99;
  min-height: 80vh;
  min-width: 60vw;
}

.featured-works {
  position: relative;
  background-color: #f7f3ec99;
  height: 20vh;
  width: auto;
  margin: 5%;
  border-radius: 10px;
}

.featured-works .container {
  position: relative;
  background-color: #ffffff;
  height: 18vh;
  width: 90%;
  border-radius: 10px;
  left: 1.5vw;
  top: 1vh;
  display: flex;
  align-items: center;
}

.featured-works .vertical-text{
  position: absolute;
  display: inline;
  top: 3vh;
  right: 1.3vw;
  font-size: 3vh;
  color: #f596aa;
}

/*精选栏超链接*/
.vertical-text a {
  text-decoration: none;
  color: #f596aa;
  transition: text-shadow 0.01s ease;
}
.vertical-text a:hover{
  text-shadow: 3px 3px 10px #f596aa;
  cursor: pointer;
}

/*个人简介*/
  .self-intro {
    position: relative;
    background: linear-gradient(to top, #f7f3ec, transparent); /* 添加渐变背景 */
    min-height: 20vh;
    width: 10vw;
    border-radius: 10px;
    margin-left: 5%;
    height: auto;
    text-align: center;
    bottom: 25vh;
}

.self-intro img {
    position: relative;
    border-radius: 50%;
    margin-top: 5%;
    cursor: pointer;
    transition: transform 1s ease;
    border: 1px solid #f596aa;
    box-shadow: 0 0 5px #f596aa50, 0 0 10px #f596aa50, 0 0 15px #f596aa50, 0 0 20px #f596aa50;  /* 添加多层阴影 */
    animation: haloEffect 1s infinite alternate;  /* 添加动画 */
}
@keyframes haloEffect {
    from {
      box-shadow: 0 0 5px #f596aa50, 0 0 10px #f596aa50, 0 0 15px #f596aa50, 0 0 20px #f596aa50;
    }
    to {
      box-shadow: 0 0 5px #f596aa, 0 0 10px #f596aa, 0 0 15px #f596aa, 0 0 20px #f596aa;
    }
  }

.self-intro img:hover {
    transform: rotate(360deg);  /* 添加旋转效果 */
  }

.self-intro a:hover{
    color: #f596aa;
    text-shadow: 3px 3px 5px #f596aa90;
    cursor: pointer;
  }

.self-p {
    display: flex;
    justify-content: space-between;
    padding-left: 20%;
    padding-right: 20%;
}

.self-intro a {
  text-decoration: none;
  color: #f596aa;
  transition: text-shadow 0.01s ease;
}
/* 精选栏作品 */
.featured-work-container {
  border: 3px solid white;
  box-shadow: 0px 0px 10px #00000030;
  z-index: 9999;
  width: 20%;
  height: 80%;
  margin-left: 2%;
  color: black;
  text-align: center;
  border-radius: 10px;
  transition: background-color 0.3s; /* 动画效果 */
}

.featured-work-container img {
  width: 50%;
  height: auto;
  float: left;
  margin-left: 3px;
}

.featured-work-container a {
  text-decoration: none;
  color: black;
}

/* 鼠标悬停时的样式 */
.featured-work-container:hover {
  background-color: #f596aa80;
  margin-bottom: 5px;
}

.featured-work-container:hover a,
.featured-work-container:hover {
  color: white; /* 文字反色 */
}

